import React, { Component } from 'react'
import { RoomContext } from '../context'
import Loading from './Loading';
import Arena from './Arena';
import Title from './Title'; 

export default class FeaturedArenas  extends Component {
    static contextType = RoomContext;
    render() {
        let { loading, featuredRooms : arenas } = this.context;
        console.log(arenas);
        arenas = arenas.map(arena => {
            return <Arena key={arena.id} arena={arena}/>
        });
        return (
 
            <section className="featured-rooms container">
                <Title  title="Featured Arenas" />
                <div className="row">
                  {loading ? <Loading/> : arenas}
                </div>
            </section>
        )
    }
}
