import React from 'react'
import RoomsFilter from './Filter';
import RoomsList from './ArenasList';
import { withRoomConsumer } from '../context';
import Loading from './Loading';

function ArenasContainer({context}){
    const{loading,sortedRooms,arenas} = context;
    if(loading)
    {
        return <Loading/>;
    }
    return(
        <>
            <RoomsFilter arenas={arenas}/>
            <RoomsList arenas = {sortedRooms}/>
        </>
    );

}
export default withRoomConsumer(ArenasContainer)
