import {Button } from "react-bootstrap";
import { CDBTable, CDBTableHeader, CDBTableBody } from "cdbreact";
import BookDataService from "../services/book.services";
import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";

///////////////////////////////////////////   Viw All Grounds Data


export const Viewallgrounds = () => {
  
  const [books, setBooks] = useState([]);
  useEffect(() => {
    getBooks();
  }, []);

  const getBooks = async () => {
    const data = await BookDataService.getAllBooks();
    console.log(data.docs);
    setBooks(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
  };

  const deleteHandler = async (id) => {
    await BookDataService.deleteBook(id);
    getBooks();
  };
    return (
      <>
      <Navbar/>
    <div className="d-flex">
    <div>
     
    </div>
    <div style={{flex:"1 1 auto", display:"flex", flexFlow:"column", height:"100vh", overflowY:"hidden"}}>
      
      {/* ////////////////////Start//////////////////////////////// */}
      <div style={{height:"100%"}}>
          <div style={{padding:"20px 1%",height:"calc(100% - 44px)",overflowY:"scroll"}}>
            <div style={{display:"grid", gridTemplateColumns:"repeat(1, minmax(300px, 980px))"}}>
              
              

                <div  className="mb-2">
                <Button variant="dark edit" onClick={getBooks}>
                  Refresh List
                </Button>
                </div>
               
                <CDBTable responsive>
                  <CDBTableHeader color="dark">
                    <tr>
                    <th>#</th>
                    <th>Name of Ground</th>
                    <th>Phone No</th>
                    <th>Capacity</th>
                    <th>Price</th>
                    <th>Payment</th>
                    <th>Date</th>
                    <th>Start Time</th>
                    <th>End Time</th>
                    </tr>
                  </CDBTableHeader>
                  <CDBTableBody> 
                  {books.map((doc, index) => {
            return (
                <tr key={doc.id}>
                <td>{index + 1}</td>
                <td>{doc.name}</td>
                <td>{doc.phoneno}</td>
                <td>{doc.capacity}</td>
                <td>{doc.price}</td>
                <td>{doc.payment}</td>
                <td>05/07/2022</td>
                <td>02:00:00</td>
                <td>03:00:00</td>
                {/* <td>{doc.date}</td>
                <td>{doc.startTime}</td>
                <td>{doc.endTime}</td> */}
                <td>
                  
                  {/* <Button
                    variant="danger"
                    className="delete"
                    onClick={(e) => deleteHandler(doc.id)}
                  >
                    Delete
                  </Button> */}
                </td>
              </tr>
            );
          })}
                  </CDBTableBody>
                </CDBTable>
                </div>
                </div>
                </div>
                
               
      
       {/* ////////////////////end//////////////////////////////// */}

    </div>
  </div>
  <Footer/>
  </>
  )
}
export default Viewallgrounds