import React from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/bootstrap/dist/js/bootstrap.min.js';
// import { BrowserRouter, Route, Switch } from "react-router-dom";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import Home from './pages/Home';
import Arenas from './pages/Arenas';
import SingleRoom from './pages/SingleArena';
import Error from './pages/Error';
import About from './pages/About';
import Contact from './pages/Contact';
import Booknow from './pages/Booknow';
import Login from './components/Login';
import Signup from './components/Signup';
import PrivateRoute from './components/PrivateRoute';
import { AuthProvider } from "./contexts/AuthContext"
import Viewallgrounds from './pages/Viewallgrounds';


function App() {
  return (
    <div className="App">
    <Router>
    <AuthProvider>
    <Switch>    
        <PrivateRoute exact path="/arenabooking" component={Home} /> 
        <PrivateRoute  path="/home" component={Home} />
      <PrivateRoute exact path="/about" component={About} />
      <PrivateRoute exact path="/contact" component={Contact} />
      <PrivateRoute exact path="/arenas" component={Arenas}/>
      <PrivateRoute exact path="/Viewallgrounds" component={Viewallgrounds}/>
      {/* <Route exact path="/bookings" component={Bookings}/> */}
      <PrivateRoute exact path="/arenas/:slug" component={SingleRoom} />
      <PrivateRoute exact path="/booknow/:slug" component={Booknow} /> 
    
 
       <Route path="/signup" component={Signup} />
        <Route path="/login" component={Login} />
        <Route component={Error}/>

      </Switch>
      </AuthProvider>
      </Router>
    
  </div>
  );
}

export default App;
